}
.search_container{
     margin-right: 35px;
    @media #{$desktop-device}{
        margin-right: 35px;
    }
    @media #{$tablet-device}{
       
        margin-right: 0;
    }
    @media #{$large-mobile}{
        margin-right: 0;
        margin-bottom: 25px;
        
    }
    @media #{$small-mobile}{
        margin-bottom: 0;
    }
    & form{
        display: flex;
        border-radius: 30px;
        border: 1px solid $grey_color;
        background: $body-color;
        width: 540px;
        @media #{$desktop-device}{
            width: 481px;
        }
        @media #{$tablet-device}{
             width: 100%;
            border-radius: 6px;
        }
         @media #{$large-mobile}{
             width: 100%;
             border-radius: 6px;
        }
        @media #{$small-mobile}{
