}
.mobail_s_none{
    @media #{$tablet-device}{
         display: none;
    }
     @media #{$large-mobile}{
        display: none;
    }
}
.mobail_s_block{
    display: none;
    @media #{$tablet-device}{
         display: block;
    }
     @media #{$large-mobile}{
        display: block;
    }
}
.search_box{
    position: relative;
    width: 100%;
    @media #{$small-mobile}{
        border: 1px solid #ededed;
        border-radius: 6px;
        margin-bottom: 25px;
    }
    & input::-webkit-input-placeholder { /* Chrome/Opera/Safari */
       color: $fonts_color;
    }
    & input::-moz-placeholder { /* Firefox 19+ */
       color: $fonts_color;
    }
    & input:-ms-input-placeholder { /* IE 10+ */
       color: $fonts_color;
    }
    & input:-moz-placeholder { /* Firefox 18- */
       color: $fonts_color;
    }
    & input{
        border: 0;
        background: inherit;
        width: 100%;
        height: 45px;
        color: $fonts_color;
        font-size: 14px;
        font-weight: 400;
        padding: 0 75px 0 20px;
        opacity: 0.7;
        @media #{$tablet-device}{
            font-size: 12px;
            padding: 0 54px 0 10px;
            width: 100%;
            height: 43px;
        }
        @media #{$large-mobile}{
            font-size: 12px;
            padding: 0 54px 0 10px;
            width: 100%;
            height: 34px;
        }
    }
    & button{
        border: 0;
        position: absolute;
        top: 0;
        height: 100%;
        line-height: 48px;
        width: 60px;
        padding: 0;
        text-align: center;
        right: 0;
        font-weight: 400;
        font-size: 20px;
        border-radius: 0 30px 30px 0;
        @include transition(.3s);
        background: $secondary_color;
        color: #fff;
        &:hover{
            background: $fonts_color;
        }
        @media #{$tablet-device}{
            width: 48px;
            padding: 0 6px;
            top: 0;
            bottom: 0;
            right: 0;
            font-size: 16px;
            line-height: 38px;
            border-radius: 0 6px 6px 0;
        }
        @media #{$large-mobile}{
            width: 48px;
            padding: 0 6px;
            top: 0;
            bottom: 0;
            right: 0;
            font-size: 16px;
            line-height: 38px;
             border-radius: 0 6px 6px 0;
        }
        
    }
}
