/*mini cart css end*/

/* 2.4 main menu css here */

.menu_position{
    position: relative;
}
.main_menu{
    @media #{$tablet-device}{
        display: none;
    }
    @media #{$large-mobile}{
        display: none;
    }
    & nav{
        & > ul{
            display: flex;
            & > li{
	            position: relative;
                margin-right: 37px;
                &:last-child{
                    margin-right: 0;
                }
                @media #{$desktop-device}{
                    margin-right: 24px;
                }
                &:hover{
                    & ul.sub_menu{
                        visibility: visible;
                        max-height: 360px;
                        padding: 24px 20px;
                    }
                    & .mega_menu{
                        visibility: visible;
                        max-height: 300px;
                        padding: 25px 30px 30px 30px;
                    }
                    & > a{
                        color: $secondary_color;
                        &::before{
                            opacity: 1;
                            visibility: visible;
                        }
                    }
                }
                
                & > a{
                    display: block;
                    font-size: 15px;
                    line-height: 50px;
                    text-transform: capitalize;
                    font-weight: 500;
                    position: relative;
                    & i{
                        margin-left: 3px;
                    }
                    &.active{
                        color: $secondary_color;
                    }
                }
                & ul.sub_menu{
                    position: absolute;
                    min-width: 220px;
                    padding: 0 20px;
                    background: $white;
                    box-shadow: 0 3px 11px 0 rgba(0,0,0,0.1);
                    max-height: 0;    
                    -webkit-transform-origin: 0 0 0;
                    transform-origin: 0 0 0;
                    left: 0;
                    right: auto;
                    visibility: hidden;
                    overflow: hidden;
                    @include transition(.5s);
                    z-index: 99;
                    top: 101%;
                    text-align: left;
                    & li{
                        & a{
                            font-size: 14px;
                            font-weight: 400;
                            display: block;
                            line-height: 32px;
                            text-transform: capitalize;
                            &:hover{
                                color: $secondary_color;
                            }
                        }
                    }
                }
                &.mega_items{
                    position: static;
                }
                
                & .mega_menu{
