        position: relative;
        color: #fff;
        &::before{
            content: "\f394";
            font-family: "Ionicons";
            color: #fff;
            display:block;
            position: absolute;
            font-size: 22px;
            line-height: 0px;
            left: 0;
            top: 50%;
            transform: translatey(-50%);
           
        }
        &::after{
            content: "\f3d0";
            font-family: "Ionicons";
            color: #fff;
            display:block;
            position: absolute;
            font-size: 13px;
            line-height: 0px;
            right: 17px;
            top: 50%;
            transform: translatey(-50%);
           
        }
        @media #{$tablet-device}{
