            float: inherit;
            text-align: right;
            padding-right: 37px;
        }
        @media #{$large-mobile}{
            line-height: 45px;
            float: inherit;
            text-align: right;
            padding-right: 37px;
        }
    }
}

.categories_menu_toggle {
	background: $white;
	position: absolute;
	width: 100%;
	top: 100%;
	z-index: 9;
    display: none;
    padding: 10px 0;
    box-shadow: 0px 0px 5.76px 0.24px rgba(0, 0, 0, 0.1);
     @media #{$tablet-device}{
        display: none;
         max-height: 360px;
        overflow: auto;
         padding: 10px 0 20px;
    }
     @media #{$large-mobile}{
        display: none;
          max-height: 350px;
        overflow: auto;
    }
    &  > ul{
        &  > li{
            position: relative;
            & > a{
                font-size: 14px;
                line-height: 38px;
                text-transform: capitalize;
                font-weight: 400;
                display: block;
                cursor: pointer;
                padding: 0 20px 0 30px;
                 @media #{$desktop-device}{
                    line-height: 38px; 
                }
                 @media #{$tablet-device}{
                   line-height: 38px;
                }
                @media #{$large-mobile}{
                    line-height: 38px;
                }
                & i.fa-angle-right{
                    float: right;
                    font-size: 15px;
                    line-height: 35px;
                    transition: .3s;
                    -webkit-transition: .3s;
                     @media #{$tablet-device}{
                        display: none;
                    }
                    @media #{$large-mobile}{
                        display: none;
                    }
                }
                &:hover{
                     @media #{$tablet-device}{
                        color: $secondary_color;
                    }
                    @media #{$large-mobile}{
                       color: $secondary_color;
                    }
                }
                
            }
            &:last-child{
                & > a{
                     border-bottom: 0;
                }
            }
            
            &:hover{
                & ul{
                    &.categories_mega_menu{
                        opacity: 1;
                        visibility: visible;
                        left: 100%;
                    } 
                }
                & > a{
                   color: $secondary_color;
                    @media #{$tablet-device}{
                        background: inherit;
                    }
                    @media #{$large-mobile}{
                        background: inherit;
                    }
                }
            }
            
            & ul{
                &.categories_mega_menu{
                    position: absolute;
                    left: 120%;
                    width: 500px;
                    background: $white;
                    padding: 25px 20px 23px;
                    overflow: hidden;
                    top: 0;
                    -webkit-transition: .3s;
                    transition: .3s;
                    z-index: 99;
                    opacity: 0;
                    visibility: hidden;
                    display: flex;
                    justify-content: space-between;
                    @media #{$desktop-device}{
                        width: 480px;
                    }
                    @media #{$large-mobile}{
                        display: none;
                        opacity: inherit !important;
                        visibility: inherit !important;
                        left: inherit !important;
                        width: 100%;
                        top: 100%;
                        max-height: 350px;
                        overflow: auto;
                        transition: unset;
                        box-shadow: inherit;
                        position: inherit;
                        padding: 0 20px 0px 60px;
                    }
                    @media #{$tablet-device}{
                        display: none;
                        opacity: inherit !important;
                        visibility: inherit !important;
                        left: inherit !important;
                        width: 100%;
                        top: 100%;
                        max-height: 350px;
                        overflow: auto;
                        transition: unset;
                        box-shadow: inherit;
                        position: inherit;
                        padding: 0 20px 0px 60px;
                    }
                    &.open{
                        display: block;
                        left: 0;
                    }
                    & > li{
                        padding: 0 15px;
                        @media #{$tablet-device}{
                            padding: 0;
                        }
                        @media #{$large-mobile}{
                            padding: 0;
                        }
                        
                        & > a{
                            line-height: 30px;
                            font-size: 14px;
                            font-weight: 500;
                            text-transform: uppercase;
                            display: block;
                            &:hover{
                                color: $secondary_color;
                            }
                            @media #{$tablet-device}{
                                line-height: 38px;
                            }
                            @media #{$large-mobile}{
                                line-height: 38px;
                            }
         
                        }
                        &.menu_item_children{
                            & > a{
                                @media #{$tablet-device}{
                                    text-transform: capitalize;
                                    font-weight: 400;
                                }
                                @media #{$large-mobile}{
                                    text-transform: capitalize;
                                    font-weight: 400;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

.categories_menu_toggle{
