     & .categories_menu_toggle > ul > li:hover > a{
        color: $secondary_color4;
    }
    & .categories_menu_toggle > ul > li ul.categories_mega_menu > li > a:hover{
        color: $secondary_color4;
    }
    & .categorie_sub_menu li a:hover{
        color: $secondary_color4;
    }
}




